  color: #3593f6;
}

h1 {
  font-size: 1.5rem;
  line-height: 1;
  padding: 1.875rem 0;
}

h2 {
  font-size: 1.25rem;
  line-height: 0.8;
  padding: 0.9375rem 0;
}

h1,
h2 {
  font-weight: bold;
}

.logo-vc {
  background: url("assets/icons/logo-vc.svg") no-repeat scroll 0 0;
  background-size: contain;
  height: 2.5rem;
  width: 8.75rem;
}

.text--red {
  color: #ff3333;
}

.text--green {
  color: #07b919;
}

.text--blue {
  color: #4da0f7;
}

.text--gray {
  color: #999;
}

.text--black {
  color: #000;
}

.form-control::-webkit-input-placeholder {
  color: #aaa;
  opacity: 1;
}
.form-control::-moz-placeholder {
  color: #aaa;
  opacity: 1;
}
.form-control:-ms-input-placeholder {
  color: #aaa;
  opacity: 1;
}
.form-control:-moz-placeholder {
  color: #aaa;
  opacity: 1;
}

.swipe {
  background: #fff;
  bottom: 0;
  display: none;
  flex-direction: column;
  flex: 0 0 18.75rem;
  position: fixed;
  top: 0;
  transition: all 0.15s;
  width: 18.75rem;
  z-index: 10000;
}
.swipe__header {
  align-items: center;
  background: #000;
  border-bottom: 1px solid #d6d6d6;
  box-sizing: content-box;
  display: flex;
  flex: 0 0 3.75rem;
  height: 3.75rem;
  padding: 0 0.9375rem;
}
.swipe__header-t {
  align-items: center;
  color: #fff;
  display: flex;
  font-weight: bold;
  width: 100%;
}
.swipe__header-link {
  color: #ff3333;
  font-size: 0.875rem;
  font-weight: normal;
  margin-left: auto;
  margin-right: 1.25rem;
}
.swipe__close {
  background: url("assets/icons/close-white.svg") no-repeat scroll 0 0;
  cursor: pointer;
  height: 1.25rem;
  width: 1.25rem;
}
.swipe__body {
  flex: 1 0 0;
  overflow-y: auto;
}
.swipe__body .btn {
  border-radius: 0;
}
.swipe .nav {
  flex-direction: column;
}
.swipe .nav .nav__item,
.swipe .nav--category .nav__item {
  border-bottom: 1px solid #d6d6d6;
  margin: 0 0.9375rem;
}
.swipe .nav .nav__link,
.swipe .nav--category .nav__link {
  align-items: center;
  display: flex;
  font-size: 0.875rem;
  height: 2.5rem;
  padding: 0;
}
.swipe .nav .nav__link--active::after,
.swipe .nav--category .nav__link--active::after {
  display: none;
}
.swipe .nav--sub {
  padding-left: 0;
}
.swipe .nav--sub .nav__item {
  border: none;
}
.swipe .nav--sub .nav__link {
  height: auto;
  padding: 0.25rem 0;
}
.swipe .order {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.swipe .order .list--total {
  margin: 0;
}
.swipe .order .order__actions {
  margin-bottom: 1.25rem;
}
.swipe .order .order__actions ~ .btn {
  border-radius: 0;
  flex: 0 0 3.125rem;
  height: 3.125rem;
  font-size: 1rem;
  font-weight: bold;
  justify-content: center;
}
.swipe .order__info {
  border: none;
  padding-top: 0;
}
.swipe .order__item {
  background: #fafafa;
}
@media (max-width: 26.25rem) {
  .swipe {
    width: 100%;
  }
}

@media (max-width: 1199.98px) {
  .swipe--category {
    display: flex;
    left: 0;
    transform: translateX(-100%);
  }

  .swipe--opened {
    transform: translateX(0);
  }
}
@media (max-width: 991.98px) {
  .swipe--menu,
.swipe--order {
    display: flex;
    right: 0;
    transform: translateX(100%);
  }

  .swipe--opened {
    transform: translateX(0);
  }
}
