  display: none;
  padding-top: 1.875rem;
  width: 100%;
}
.tabs .tabs__input:nth-of-type(1):checked ~ .tabs__content:nth-of-type(1) {
  display: block;
}
.tabs .tabs__input:nth-of-type(2):checked ~ .tabs__content:nth-of-type(2) {
  display: block;
}
.tabs .tabs__input:nth-of-type(3):checked ~ .tabs__content:nth-of-type(3) {
  display: block;
}
.tabs .tabs__input:nth-of-type(4):checked ~ .tabs__content:nth-of-type(4) {
  display: block;
}
.tabs .tabs__input:nth-of-type(5):checked ~ .tabs__content:nth-of-type(5) {
  display: block;
}
.tabs .tabs__input:nth-of-type(6):checked ~ .tabs__content:nth-of-type(6) {
  display: block;
}
.tabs .tabs__input:nth-of-type(7):checked ~ .tabs__content:nth-of-type(7) {
  display: block;
}
.tabs .tabs__input:nth-of-type(8):checked ~ .tabs__content:nth-of-type(8) {
  display: block;
}
.tabs .tabs__input:nth-of-type(9):checked ~ .tabs__content:nth-of-type(9) {
  display: block;
}
.tabs .tabs__input:nth-of-type(10):checked ~ .tabs__content:nth-of-type(10) {
  display: block;
}
@media (max-width: 767.98px) {
  .tabs {
    padding: 0 1.25rem;
  }
  .tabs__item {
    font-size: 0;
  }
  .tabs__item .mobile-name {
    font-size: 0.875rem;
  }
}

.table-responsive {
  display: block;
  width: 100%;
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
}

.table {
  border-collapse: collapse;
  margin-bottom: 0.625rem;
  width: 100%;
}
.table__icon {
  display: inline-block;
  height: 1.25rem;
  vertical-align: middle;
}
.table__icon ~ .table__icon, .table__icon ~ .form-switch {
  margin-left: 0.625rem;
}
.table .col-w-340 {
  flex: 0 0 21.25rem;
  min-width: 21.25rem;
}
.table .col-w-260 {
  flex: 0 0 16.25rem;
  min-width: 16.25rem;
}
.table .col-w-160 {
  flex: 0 0 10rem;
  min-width: 10rem;
}
.table .col-w-140 {
  flex: 0 0 8.75rem;
  min-width: 8.75rem;
}
.table .col-w-130 {
  flex: 0 0 8.125rem;
  min-width: 8.125rem;
}
.table .col-w-120 {
  flex: 0 0 7.5rem;
  min-width: 7.5rem;
}
.table .col-w-100 {
  flex: 0 0 6.25rem;
  min-width: 6.25rem;
}
.table .col-w-80 {
  flex: 0 0 5rem;
  min-width: 5rem;
}
.table th,
.table td {
  border: 1px solid #d6d6d6;
  color: #000;
  font-size: 0.875rem;
  min-height: 2.5rem;
  min-width: 2.5rem;
  padding: 0.625rem;
  text-align: left;
  vertical-align: top;
}
.table th {
  background-color: #f0f0f0;
  color: #aaa;
  font-weight: bold;
}
.table tr:nth-child(even) td {
  background-color: #f8f8f8;
}